  max-width: 95vw;
  z-index: 1000;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.item {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 5px;
  max-height: 20vh;
  height: 20vh;
  max-width: 24vh; }
  @media only screen and (max-width: 600px) {
    .item {
      max-height: none;
      max-height: initial;
      height: auto;
      height: initial;
      max-width: 25vw; } }
  .item img {
    max-height: 20vh;
    height: 20vh; }
    @media only screen and (max-width: 600px) {
      .item img {
        max-height: none;
        max-height: initial;
        height: auto;
        height: initial;
        max-width: 25vw; } }

.stylizedList {
  position: relative;
  z-index: 0;
  background-color: #f3f3f3;
  border: 1px solid #efefef;
  border-radius: 3px;
  outline: none; }

.stylizedItem {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  padding: 0 20px;
  background-color: #fff;
  border-bottom: 1px solid #efefef;
  box-sizing: border-box;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #333;
  font-weight: 400; }

.disabled {
  cursor: not-allowed;
  opacity: 0.5; }

.handle {
  display: block;
  width: 18px;
  height: 18px;
  opacity: 0.25;
  margin-right: 20px;
  cursor: row-resize; }

.horizontalList {
  display: -webkit-flex;
  display: flex;
  width: 600px;
  max-width: 100vw;
  white-space: nowrap; }

.horizontalItem {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 200px;
  border-right: 1px solid #efefef;
  border-bottom: 0; }

.grid {
  display: block;
  width: 520px;
  height: 350px;
  white-space: nowrap;
  border: 0;
  background-color: transparent; }

.gridItem {
  float: left;
  width: 130px;
  padding: 8px;
  background: transparent;
  border: 0; }
  .gridItem .wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
    height: 100%;
    background: #fff;
    border: 1px solid #efefef;
    font-size: 28px; }
    .gridItem .wrapper span {
      display: none; }

.category {
  height: auto; }
  .category .categoryHeader {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px 14px;
    background: #f9f9f9;
    border-bottom: 1px solid #efefef;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .category .categoryList {
    height: auto; }

.divider {
  padding: 10px 20px;
  background: #f9f9f9;
  border-bottom: 1px solid #efefef;
  text-transform: uppercase;
  font-size: 14px;
  color: #333; }

.helper {
  box-shadow: 0 5px 5px -5px rgba(0, 0, 0, 0.2), 0 -5px 5px -5px rgba(0, 0, 0, 0.2); }

.stylizedHelper {
  box-shadow: 0 5px 5px -5px rgba(0, 0, 0, 0.2), 0 -5px 5px -5px rgba(0, 0, 0, 0.2);
  background-color: rgba(255, 255, 255, 0.8);
  cursor: row-resize; }
  .stylizedHelper.horizontalItem {
    cursor: col-resize; }
  .stylizedHelper.gridItem {
    background-color: transparent;
    white-space: nowrap;
    box-shadow: none; }
    .stylizedHelper.gridItem .wrapper {
      background-color: rgba(255, 255, 255, 0.8);
      box-shadow: 0 0 7px rgba(0, 0, 0, 0.15); }

.shrinkedHelper {
  height: 20px !important; }

body {
  font-family: 'Montserrat', 'Helvetica Neue', 'Helvetica', arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
